.App {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

.App-header {
  margin-bottom: 30px;
  text-align: center;
}

.App-header h1 {
  color: #2c3e50;
  font-size: 32px;
  margin-bottom: 10px;
}

/* App mode selector styles */
.app-mode-selector {
  display: flex;
  justify-content: center;
  margin: 20px 0;
  gap: 15px;
}

.mode-btn {
  padding: 10px 20px;
  background-color: #f8f9fa;
  color: #343a40;
  border: 1px solid #dee2e6;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: all 0.3s ease;
  min-width: 150px;
}

.mode-btn:hover {
  background-color: #e9ecef;
}

.mode-btn.active {
  background-color: #343a40;
  color: white;
  border-color: #212529;
}

/* Chapter selector styles */
.chapter-selector {
  margin: 20px auto;
  text-align: center;
  width: 100%;
  max-width: 300px;
}

.chapter-dropdown {
  width: 100%;
  padding: 12px;
  font-size: 16px;
  border: 2px solid #4CAF50;
  border-radius: 8px;
  background-color: white;
  color: #333;
  cursor: pointer;
  outline: none;
  transition: all 0.3s ease;
}

.chapter-dropdown:hover {
  border-color: #45a049;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.chapter-dropdown:focus {
  border-color: #45a049;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

/* Remove old chapter button styles if they exist */
.chapter-btn {
  display: none;
}

main {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
}

.quiz-container {
  width: 900px;
  max-width: 100%;
  margin: 0 auto;
}

.statistics {
  width: 900px;
  max-width: 100%;
  margin: 0 auto;
}

@media (min-width: 992px) {
  main {
    flex-direction: column;
    align-items: center;
  }
  
  .App-header {
    margin-bottom: 40px;
  }
}

.App-footer {
  margin-top: 40px;
  text-align: center;
  color: #6c757d;
  padding: 20px 0;
  border-top: 1px solid #eee;
}

.error-message {
  background-color: #f8d7da;
  color: #721c24;
  padding: 15px;
  border-radius: 5px;
  border: 1px solid #f5c6cb;
  margin: 20px 0;
  text-align: center;
}

h1 {
  color: #2c3e50;
  text-align: center;
}

.btn {
  display: inline-block;
  padding: 10px 20px;
  background-color: #3498db;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  margin-right: 10px;
}

.btn:hover {
  background-color: #2980b9;
}

.btn-check {
  background-color: #28a745;
}

.btn-check:hover {
  background-color: #218838;
}

/* Responsive styles for app mode and chapter selectors */
@media (max-width: 768px) {
  .app-mode-selector {
    flex-direction: column;
    width: 100%;
  }
  
  .mode-btn {
    width: 100%;
  }
  
  .chapter-selector {
    flex-direction: column;
    width: 100%;
    max-width: 300px;
  }
  
  .chapter-btn {
    width: 100%;
  }
  
  .quiz-container,
  .statistics {
    width: 100%;
    padding: 0 15px;
  }
} 